<template>
  <v-app-bar
    app
    :elevation="elevation"
    :class="
      store.isDark == true || store.isDark == 'true'
        ? 'appBarClassDark'
        : 'appBarClass'
    "
    class=""
  >
    <div aria-label="headerpage">
      <v-img
        v-motion-pop
        :width="mdAndUp ? '50' : '50'"
        class="pt-4 mr-6"
        alt="logo2"
        src="../../../assets/logo.png"
      >
      </v-img>
    </div>
    <p class="hidden-sm-and-down pr-5">{{ $route.name }}</p>
    <v-spacer></v-spacer>

    <v-menu>
      <template v-slot:activator="{ props }">
        <v-btn icon v-bind="props">
          <v-avatar v-if="store.userData && store.userData.photoURL">
            <v-img :src="store.userData.photoURL"></v-img>
          </v-avatar>
          <v-icon v-else size="50">mdi-account-circle</v-icon>
        </v-btn>
      </template>
      <v-card min-width="300">
        <v-list>
          <v-list-item
            :prepend-avatar="
              store.userData.photoURL
                ? store.userData.photoURL
                : 'https://w7.pngwing.com/pngs/178/595/png-transparent-user-profile-computer-icons-login-user-avatars-thumbnail.png'
            "
            :title="
              store.userData.displayName
                ? store.userData.displayName
                : 'your name'
            "
            :subtitle="store.userData.email"
          >
          </v-list-item>
          <v-divider></v-divider>

          <v-list-item
            v-for="(item, index) in items"
            :key="index"
            :value="index"
            :to="item.to"
          >
            <v-list-item-title>{{ item.title }}</v-list-item-title>
          </v-list-item>
          <v-list-item>
            <v-btn
              @click="handleSignOut"
              v-if="isLoggedIn"
              color="error"
              variant="outlined"
              block
              >تسجيل الخروج</v-btn
            >
          </v-list-item>
        </v-list>
      </v-card>
    </v-menu>
  </v-app-bar>
</template>

<script>
import { auth } from "../../../firebase.js";

import { useTheme, useDisplay } from "vuetify";
import { useCounterStore } from "@/store/index.js";
import { onMounted, ref } from "vue";
import { getAuth, onAuthStateChanged, signOut } from "firebase/auth";
import { useRouter } from "vue-router";
export default {
  setup() {
    const items = ref([
      { title: "كل الرحلات", to: "/dashboard/allTravelsPage" },
      { title: "رحلاتي", to: "/dashboard/myTravelPage" },
    ]);
    const router = useRouter();
    const theme = useTheme();
    const { mdAndUp, xlAndUp } = useDisplay();
    const store = useCounterStore();
    function toggleTheme() {
      if (theme.global.name.value == "dark") {
        theme.global.name.value = "light";
        store.isDark = false;
        localStorage.setItem("isDark", false);
      } else {
        theme.global.name.value = "dark";
        store.isDark = true;
        localStorage.setItem("isDark", true);
      }
    }

    const isLoggedIn = ref(false);
    let auth;
    onMounted(() => {
      auth = getAuth();
      onAuthStateChanged(auth, (user) => {
        if (user) {
          isLoggedIn.value = true;
        } else {
          isLoggedIn.value = false;
        }
      });
    });
    const handleSignOut = () => {
      signOut(auth).then(() => {
        store.userData = null;
        localStorage.setItem("userData", null);
        router.push("/");
      });
    };
    return {
      isLoggedIn,
      handleSignOut,
      mdAndUp,
      store,
      xlAndUp,
      toggleTheme,
      theme,
      items,
    };
  },
  data() {
    return {
      isScrolled: false,
      elevation: 0,
      activeItemIndex: 0,
    };
  },
  computed: {
    appBarClass() {
      return {
        primary: this.isScrolled,
        transparent: !this.isScrolled,
      };
    },
    appBarClassDark() {
      return {
        primary: this.isScrolled,
        transparent: !this.isScrolled,
      };
    },
  },

  methods: {
    chanageLang() {
      if (this.$i18n.locale == "ar") {
        this.$i18n.locale = "en";
        this.$vuetify.locale.rtl.ar = false;
      } else {
        this.$i18n.locale = "ar";
        this.$vuetify.locale.rtl.ar = true;
      }

      localStorage.setItem("lang", this.$i18n.locale);
    },
  },
};
</script>
<style scoped>
.v-application--is-scrolled .v-app-bar {
  transition: background-color 0.3s ease, box-shadow 0.3s ease;
}
.green--text {
  color: green !important;
}
.appBarClass {
  background-color: rgba(
    255,
    255,
    255,
    0.5
  ); 
  backdrop-filter: blur(4px);
}
.appBarClassDark {
  background-color: rgba(
    5,
    12,
    26,
    0.5
  ); 
  backdrop-filter: blur(4px);
}
</style>
